import { ActivatedRoute, Router } from '@angular/router';
import { Component, OnInit, Input, Output, EventEmitter } from '@angular/core';
import { Validators, FormControl, FormGroup, FormBuilder } from '@angular/forms';

import { SelectItem } from 'primeng/primeng';
import { ConfirmationService } from 'primeng/primeng';//
import { StepsModule, MenuItem, Message } from 'primeng/primeng';

import { Commfunc } from 'app/@uisftech/common/service/commonFun';
import { ComponentConfigBean } from "./bean/component-config.bean";
import { tableMessage } from 'app/@uisftech/common/constant/config';
import { CreditCcmService } from 'app/pages/tzb/custom/credit-ccm/common/http/credit-ccm.http.service';

@Component({
    selector: 'app-product-component-config',
    templateUrl: './component-config.component.html',
    styleUrls: ['./component-config.component.scss'],
    providers: [CreditCcmService, ConfirmationService,Commfunc]
})
/**
 * 
 */
export class ComponentConfigComponent implements OnInit {
    public tabMesg: any = tableMessage;//
    public userform: FormGroup; //
    // bean
    public componentConfigBean: ComponentConfigBean = new ComponentConfigBean();
    //
    public updateValue: any = [];
    select_1: SelectItem[] = [];//
    select_2: SelectItem[] = [];//
    public vlaue1: any;
    public vlaue2: any;//
    items: MenuItem[];
    //
    public all_title: string = '';
    public ceshi0: any;
    public ceshi: any;
    public featureType: any;
    code: any;
    //
    public showModel: string;
    display: boolean = false;
    //
    public headerTitle: string
    public cars_2: any;//
    public cars_2Vluel: any;//

    activeIndex: number = 0;
    //
    public msgs: Message[] = [];
    public accept_value: any = [];
    //
    pageSize: number = 10;
    pageNum: number = 1;
    total: string;

    constructor(public creditCcmService: CreditCcmService,
        public confirmationService: ConfirmationService,
        public router: Router,
        public fb: FormBuilder,
        public commfunc: Commfunc,  ) {
        this.userform = fb.group({
            'description': new FormControl('', [Validators.pattern(/^\S+$/), Validators.required]),
            parentCategoryId: [''],
        });
        this.codeValues() //
        this.featureType = this.code['FeatureType'];
    }

    ngOnInit() {
        this.vlaue1 = 0;
        this.select_2 = [
            { label: '', value: " " },
        ];
        this.items = [{
            label: '',
            command: (event: any) => {
                this.activeIndex = 0;
            }
        },
        {
            label: '',
            command: (event: any) => {
                this.activeIndex = 1;
            }
        },];
    }
    //
    codeValues() {
        this.commfunc.codeValue();
        let ret = this.commfunc.codeDatas;
        return this.code = ret //code
    }
    prev() {
        if (this.activeIndex > 0) {
            this.activeIndex = this.activeIndex - 1;
            this.vlaue1 = this.activeIndex;
            this.vlaue2 = this.ceshi;
        }
    }
    next() {
        if (this.activeIndex == 0) {
            // 
            for (const i in this.userform.controls) {
                this.userform.controls[i].markAsDirty();
            }
            //
            for (const i in this.userform.controls) {
                if (!this.userform.controls[i].valid) {
                    return;
                }
            }
            //
            let redo_name = {
                name: this.componentConfigBean.description,
                type: 'PRODUCT_FEATURE_CATEGORY'
            }
            this.creditCcmService.queryNameIsNotExist(redo_name).subscribe(data => {
                if (data.returnCode.code == "success") {
                    if (data.isOrNot == "is") {
                        this.msgs = [];
                        this.msgs.push({ severity: 'error', summary: '', detail: "" });
                    } else if (data.isOrNot == "not") {
                        //
                        this.creditCcmService.addFeatureCategory(this.componentConfigBean).subscribe(data => {
                            if (data.returnCode.code == "success") {
                                this.accept_value = data;
                                this.activeIndex = this.activeIndex + 1;
                                this.vlaue1 = this.activeIndex;
                                this.vlaue2 = this.ceshi;
                                this.msgs = [];
                                this.msgs.push({ severity: 'success', summary: '', detail: data.returnCode.message });
                            } else {
                                this.msgs = [];
                                this.msgs.push({ severity: 'error', summary: '', detail: data.returnCode.message });
                            }
                        }, error => {
                            this.msgs = [];
                            this.msgs.push({ severity: 'error', summary: '', detail: "" });
                        })
                    }
                } else {
                    this.msgs = [];
                    this.msgs.push({ severity: 'error', summary: '', detail: data.returnCode.message });
                }
            }, error => {
                this.msgs = [];
                this.msgs.push({ severity: 'error', summary: '', detail: "" });
            })
        }
    }

    confirm() {
        this.router.navigate(['/pages/tzb/custom/credit-ccm/productComponentConfig/product-component-view'])
    }
    //
    attr_query() {
        let param = {
            productFeatureCategoryId: this.accept_value.productFeatureCategoryId,
            pageSize: this.pageSize,
            pageNum: this.pageNum
        };
        this.creditCcmService.prodFeatureCategoryDetails(param).subscribe(data => {
            if (data.returnCode.code == "success") {
                this.total = data.total;
                data.resultList.forEach(item => {
                    if (item.min == null) {
                        if (item.def != null) {
                            item.featureValue = 3;
                        } else {
                            item.featureValue = 1;
                        }
                    }
                    if (item.min != null) {
                        item.featureValue = 2;
                    }
                });
                this.cars_2 = data.resultList;
            } else {
                this.msgs = [];
                this.msgs.push({ severity: 'error', summary: '', detail: data.returnCode.message });
            }
        }, error => {
            this.msgs = [];
            this.msgs.push({ severity: 'error', summary: '', detail: "" });
        })
    }
    //
    addAttribute(item) {
        this.updateValue = this.accept_value.productFeatureCategoryId;
        this.display = true;
        this.headerTitle = '';
        this.showModel = '1';
    }
    //
    addCall(param) {
        if (param.code == 'cancelModel') {
            this.display = param.display;
        } else {
            this.msgs = [];
            this.msgs.push({ severity: 'success', summary: '', detail: param.code });
            this.display = param.display;
        }
        this.attr_query();
    }
    //
    modifyAttribute(item) {
        this.display = true;
        this.headerTitle = '';
        this.showModel = '2';
        this.updateValue = item.productFeatureTypeId;
    }
    //
    modifyCall(param) {
        if (param.code == 'cancelModel') {
            this.display = param.display;
        } else {
            this.msgs = [];
            this.msgs.push({ severity: 'success', summary: '', detail: param.code });
            this.display = param.display;
        }
        this.attr_query();
    }
    //
    detailsClick(item) {
        this.display = true;
        this.headerTitle = '';
        this.showModel = '3';
        this.updateValue = item.productFeatureTypeId;
    }
    //
    detailsCall(param) {
        this.display = param;
    }
    //
    deleteClick(item) {
        let param = { productFeatureTypeId: item.productFeatureTypeId, productFeatureCategoryId: this.accept_value.productFeatureCategoryId };
        this.confirmationService.confirm({
            message: '?',
            accept: () => {
                this.creditCcmService.prdcFeaAndFeaTypeDel(param).subscribe(data => {
                    if (data.returnCode.code == "success") {
                        this.msgs = [];
                        this.msgs.push({ severity: 'success', summary: '', detail: data.returnCode.message });
                        this.attr_query();
                    } else {
                        this.msgs = [];
                        this.msgs.push({ severity: 'error', summary: '', detail: data.returnCode.message });
                    }
                }, error => {
                    this.msgs = [];
                    this.msgs.push({ severity: 'error', summary: '', detail: "" });
                })
            }
        });
    }
    //
    paginate(event) {
        //
        this.pageSize = event.rows;
        //
        let currentPage = event.page + 1;
        this.pageNum = currentPage;
        //
        this.attr_query();
        this.pageNum = 1;
    }
}





