import { ModuleWithProviders } from '@angular/core';
import { Routes, RouterModule } from '@angular/router';

import { ComponentViewComponent } from './component-view/component-view.component';//
import { ComponentConfigComponent } from './component-config/component-config.component';//
import { ComponentModifyComponent } from './component-modify/component-modify.component';//-

const routes: Routes = [
    {
        path: 'product-component-view', component: ComponentViewComponent,
    },
    {
        path: 'product-component-config', component: ComponentConfigComponent,
    },
    {
        path: 'product-component-modify', component: ComponentModifyComponent,
    },
];

export const CreditProductComponentRouting: ModuleWithProviders = RouterModule.forChild(routes);
