import { ActivatedRoute, Router } from '@angular/router';
import { Component, OnInit, Input, Output, EventEmitter } from '@angular/core';

import { SelectItem } from 'primeng/primeng';
import { ConfirmationService } from 'primeng/primeng';//
import { StepsModule, MenuItem, Message } from 'primeng/primeng';

import { Commfunc } from 'app/@uisftech/common/service/commonFun';
import { tableMessage } from 'app/@uisftech/common/constant/config';
import { Validators, FormControl, FormGroup, FormBuilder } from '@angular/forms';
import { ComponentConfigBean } from "./../component-config/bean/component-config.bean";
import { CreditCcmService } from 'app/pages/tzb/custom/credit-ccm/common/http/credit-ccm.http.service';

@Component({
    selector: 'app-product-component-modify',
    templateUrl: './component-modify.component.html',
    styleUrls: ['./component-modify.component.scss'],
    providers: [CreditCcmService, ConfirmationService, Commfunc]
})

export class ComponentModifyComponent implements OnInit {
    public userform: FormGroup; //
    tabMesg: any = tableMessage;//
    // bean
    componentConfigBean: ComponentConfigBean = new ComponentConfigBean();
    //
    updateValue: any = [];
    select_1: SelectItem[] = [];//
    select_2: SelectItem[] = [];//
    vlaue1: any;
    vlaue2: any;//
    items: MenuItem[];
    //
    all_title: string = '';
    ceshi0: any;
    ceshi: any;
    featureType: any;
    code: any;
    //
    showModel: string;
    display: boolean = false;
    //
    headerTitle: string
    cars_2: any;//
    cars_2Vluel: any;//

    activeIndex: number = 0;
    //
    msgs: Message[] = [];
    accept_value: any = []
    //ID
    productFeatureCategoryId: any;//ID
    //
    pageSize: number = 10;
    pageNum: number = 1;
    total: string;

    constructor(
        public commfunc: Commfunc,
        public creditCcmService: CreditCcmService,
        public confirmationService: ConfirmationService,
        public router: Router,
        public routers: ActivatedRoute,
        public fb: FormBuilder, ) {
        this.userform = fb.group({
            'description': new FormControl('', [Validators.pattern(/^\S+$/), Validators.required]),
            parentCategoryId: [''],
        })
        this.codeValues() //
        this.featureType = this.code['FeatureType'];
    }

    ngOnInit() {
        this.routers.params.subscribe((data) => {
            this.productFeatureCategoryId = data.productFeatureCategoryId;
            if (data.description == "null") {
                this.componentConfigBean.description = ""
            } else {
                this.componentConfigBean.description = data.description;
            }
            if (data.parentCategoryId == "null") {
                this.componentConfigBean.parentCategoryId = ""
            } else {
                this.componentConfigBean.parentCategoryId = data.parentCategoryId;
            }

            this.accept_value.productFeatureCategoryId = data.productFeatureCategoryId;
        })
        this.queryEventA(this.productFeatureCategoryId);

        this.vlaue1 = 0;
        this.select_2 = [
            { label: '', value: " " },
        ];
        this.items = [{
            label: '',
            command: (event: any) => {
                this.activeIndex = 0;
            }
        },
        {
            label: '',
            command: (event: any) => {
                this.activeIndex = 1;
            }
        },];
    }
    //
    codeValues() {
        this.commfunc.codeValue();
        let ret = this.commfunc.codeDatas;
        return this.code = ret //code
    }
    queryEventA(itemV) {
        let param = {
            productFeatureCategoryId: itemV,
            pageSize: this.pageSize,
            pageNum: this.pageNum
        };
        this.creditCcmService.prodFeatureCategoryDetails(param).subscribe(data => {
            if (data.returnCode.code == "success") {
                this.total = data.total;
                data.resultList.forEach(item => {
                    if (item.description != null) {
                        item.featureValue = 1;
                    }
                    if (item.min != null) {
                        item.featureValue = 2;
                    }
                    if (item.def != null && item.min == null) {
                        item.featureValue = 3;
                    }

                });
                this.cars_2 = data.resultList;
            } else {
                this.msgs = [];
                this.msgs.push({ severity: 'error', summary: '', detail: data.returnCode.message });
            }
        }, error => {
            this.msgs = [];
            this.msgs.push({ severity: 'error', summary: '', detail: "" });
        });
    }

    prev() {
        if (this.activeIndex > 0) {
            this.activeIndex = this.activeIndex - 1;
            this.vlaue1 = this.activeIndex;
            this.vlaue2 = this.ceshi;
        }
    }
    next() {
        if (this.activeIndex == 0) {
            // 
            for (const i in this.userform.controls) {
                this.userform.controls[i].markAsDirty();
            }
            //
            for (const i in this.userform.controls) {
                if (!this.userform.controls[i].valid) {
                    return;
                }
            }
            let parm = {
                productFeatureCategoryId: this.productFeatureCategoryId,
                parentCategoryId: this.componentConfigBean.parentCategoryId,
                description: this.componentConfigBean.description,
            }
            //
            let redo_name = {
                xxId: this.productFeatureCategoryId,
                name: this.componentConfigBean.description,
                type: 'PRODUCT_FEATURE_CATEGORY'
            }
            this.creditCcmService.checkBeforeUpdate(redo_name).subscribe(data => {
                if (data.returnCode.code == "success") {
                    if (data.isOrNotUpdate == "is") {
                        //
                        this.creditCcmService.updateFeatureCategory(parm).subscribe(data => {
                            if (data.returnCode.code == "success") {
                                this.accept_value = parm
                                this.activeIndex = this.activeIndex + 1;
                                this.vlaue1 = this.activeIndex;
                                this.vlaue2 = this.ceshi;
                            } else {
                                this.msgs = [];
                                this.msgs.push({ severity: 'error', summary: '', detail: data.returnCode.message });
                            }
                        }, error => {
                            this.msgs = [];
                            this.msgs.push({ severity: 'error', summary: '', detail: "" });
                        })
                    } else {
                        this.msgs = [];
                        this.msgs.push({ severity: 'error', summary: '', detail: data.retDesc + "," });
                    }
                } else {
                    this.msgs = [];
                    this.msgs.push({ severity: 'error', summary: '', detail: data.returnCode.message });
                }
            }, error => {
                this.msgs = [];
                this.msgs.push({ severity: 'error', summary: '', detail: "" });
            })

        }
    }

    confirm() {
        this.router.navigate(['/pages/tzb/custom/credit-ccm/productComponentConfig/product-component-view'])
    }
    //
    addAttribute(item) {
        this.updateValue = this.accept_value.productFeatureCategoryId;
        this.display = true;
        this.headerTitle = '';
        this.showModel = '1';
    }
    //
    addCall(param) {
        if (param.code == 'cancelModel') {
            this.display = param.display;
        } else {
            this.msgs = [];
            this.msgs.push({ severity: 'success', summary: '', detail: param.code });
            this.display = param.display;
        }
        this.queryEventA(this.accept_value.productFeatureCategoryId);
    }
    //
    modifyAttribute(item) {
        this.display = true;
        this.headerTitle = '';
        this.showModel = '2';
        this.updateValue = item.productFeatureTypeId;
    }
    //
    modifyCall(param) {
        if (param.code == 'cancelModel') {
            this.display = param.display;
        } else {
            this.msgs = [];
            this.msgs.push({ severity: 'success', summary: '', detail: param.code });
            this.display = param.display;
        }
        this.queryEventA(this.accept_value.productFeatureCategoryId);
    }
    //
    detailsClick(item) {
        this.display = true;
        this.headerTitle = '';
        this.showModel = '3';
        this.updateValue = item.productFeatureTypeId;
    }
    //
    detailsCall(param) {
        this.display = param;
    }
    //
    deleteClick(item) {
        let param = { productFeatureTypeId: item.productFeatureTypeId, productFeatureCategoryId: this.accept_value.productFeatureCategoryId };
        this.confirmationService.confirm({
            message: '?',
            accept: () => {
                this.creditCcmService.prdcFeaAndFeaTypeDel(param).subscribe(data => {
                    if (data.returnCode.code == "success") {
                        this.msgs = [];
                        this.msgs.push({ severity: 'success', summary: '', detail: data.returnCode.message });
                        this.queryEventA(this.accept_value.productFeatureCategoryId);
                    } else {
                        this.msgs = [];
                        this.msgs.push({ severity: 'error', summary: '', detail: data.returnCode.message });
                    }
                }, error => {
                    this.msgs = [];
                    this.msgs.push({ severity: 'error', summary: '', detail: "" });
                })
            }
        });
    }
    //
    paginate(event) {
        //
        this.pageSize = event.rows;
        //
        let currentPage = event.page + 1;
        this.pageNum = currentPage;
        //
        this.queryEventA(this.accept_value.productFeatureCategoryId);
        this.pageNum = 1;
    }
}





