import { Component, OnInit } from '@angular/core';

import { Message } from 'primeng/primeng';
import { SelectItem } from 'primeng/primeng';
import { ConfirmationService } from 'primeng/primeng';//

import { ComponentViewBean } from "./bean/component-view.bean";
import { Commfunc } from "app/@uisftech/common/service/commonFun";
import { tableMessage } from 'app/@uisftech/common/constant/config';
import { CreditCcmService } from 'app/pages/tzb/custom/credit-ccm/common/http/credit-ccm.http.service';

@Component({
  selector: 'app-product-component-view',
  templateUrl: './component-view.component.html',
  styleUrls: ['./component-view.component.scss'],
  providers: [CreditCcmService, ConfirmationService, Commfunc]
})

export class ComponentViewComponent implements OnInit {
  tabMesg: any = tableMessage;//
  //
  updateValue: any = [];
  // bean
  componentViewBean: ComponentViewBean = new ComponentViewBean();

  select_1: SelectItem[] = [];//
  select_line: any = [];//
  select_group: any = [];//
  line_value: string;//
  group_value: string;//

  //
  all_title: string = '';
  //
  cars_2: any;
  //
  total: string;
  //
  pageSize: string = '10';
  //
  showModel: string;
  headerTitle: string
  display: boolean = false;
  //
  msgs: Message[] = [];
  //
  first: number = 0;
  constructor(
    public creditCcmService: CreditCcmService,
    public confirmationService: ConfirmationService,
    public commfunc: Commfunc) {
    this.select_1 = [
      { label: '', value: " " },
    ];
    this.select_line = [
      { label: '', value: " " },
    ];
    this.select_group = [
      { label: '', value: " " },
    ];

  }

  ngOnInit() {
    this.select_line = [
      { label: '', value: " " },
    ];
    this.select_group = [
      { label: '', value: " " },
    ];
    this.componentViewBean.pageSize = 10;
    this.componentViewBean.pageNum = 1;
    this.first = 0;
    this.queryClick();
  }

  // 
  resetClick() {
    this.componentViewBean = new ComponentViewBean();
  }

  //
  querySecle() {
    this.componentViewBean.pageSize = 10;
    this.componentViewBean.pageNum = 1;
    this.first = 0;
    this.queryClick();
  }

  //
  queryClick() {
    this.componentViewBean.productCategoryLineId = this.line_value;
    this.componentViewBean.productCategoryGroupId = this.group_value;
    this.creditCcmService.productFeatureCategoryList(this.componentViewBean).subscribe(data => {
      if (data.returnCode.code == "success") {
        this.cars_2 = data.productFeatureCategoryList;
        this.total = data.total;
      } else {
        this.msgs = [];
        this.msgs.push({ severity: 'error', summary: '', detail: data.returnCode.message });
      }
    }, error => {
      this.msgs = [];
      this.msgs.push({ severity: 'error', summary: '', detail: '' });
    })
  }
  // 
  detailsClick(item) {
    this.display = true;
    this.headerTitle = '';
    this.updateValue = item;
    this.showModel = '1';
  }
  //
  detailsCall(param) {
    this.display = param;
    // this.queryClick();
  }
  //
  deleteClick(item) {
    this.confirmationService.confirm({
      message: '?',
      accept: () => {
        //
        let param = { productFeatureCategoryId: item.productFeatureCategoryId };
        this.creditCcmService.delFeatureCategory(param).subscribe(data => {
          if (data.returnCode.code == "success") {
            this.msgs = [];
            this.msgs.push({ severity: 'success', summary: '', detail: data.returnCode.message });
            this.first = 0;
            this.queryClick();
          } else {
            this.msgs = [];
            this.msgs.push({ severity: 'error', summary: '', detail: data.returnCode.message });
          }
        }, error => {
          this.msgs = [];
          this.msgs.push({ severity: 'error', summary: '', detail: "" });
        })
      }
    });
  }
  //
  paginate(event) {
    //
    this.componentViewBean.pageSize = event.rows;
    //
    let currentPage = event.page + 1;
    this.componentViewBean.pageNum = currentPage;
    this.first = (currentPage - 1) * this.componentViewBean.pageSize;
    //
    this.queryClick();
    this.componentViewBean.pageNum = 1;
  }
  //
  permissionCheck(id) {
    return this.commfunc.permissionCheck(id)
  }
}
